<script>
import "sweetalert2/dist/sweetalert2.css";
import HtmlEditor from "@/components/Inputs/HtmlEditor";
import DropzoneFileUpload from "@/components/Inputs/DropzoneFileUpload";
import flatPicker from "vue-flatpickr-component";
import "flatpickr/dist/flatpickr.css";
import { Table, TableColumn, Select, Option } from "element-ui";
import { BasePagination, BaseInput } from "@/components";
import clientPaginationMixin from "@/components/clientPaginationMixin";
import swal from "sweetalert2";
import users from "./users2";
const axios = require("axios");
import moment from "moment";
import cabang from "../../cabang";

export default {
  mixins: [clientPaginationMixin],
  components: {
    HtmlEditor,
    BasePagination,
    BaseInput,
    flatPicker,
    DropzoneFileUpload,
    [Select.name]: Select,
    [Option.name]: Option,
    [Table.name]: Table,
    [TableColumn.name]: TableColumn
  },
  data() {
    return {
      progress: {
        petaZonasi: 0,
        petaBatas: 0,
        petaRKL: 0,
        petaRPL: 0
      },
      isUpdate: false,
      allPeta: {
        petaZonasi: true,
        petaBatas: false,
        petaRKL: false,
        petaRPL: false
      },
      tempEdit: null,
      petaRPLName: "",
      petaRKLName: "",
      petaBatasName: "",
      petaZonasiName: "",
      file: [],
      formIzinLingkungan: {
        uk_id: null,
        il_name: "",
        il_content: "",
        il_mapzonasi_link: "",
        il_mapbaswil_link: "",
        il_maprkl_link: "",
        il_maprpl_link: "",
        il_nomorizin: "",
        date: ""
      },
      formIzinLingkunganDetail: {
        uk_id: null,
        il_name: "",
        il_content: "",
        il_mapzonasi_link: "",
        il_mapbaswil_link: "",
        il_maprkl_link: "",
        il_maprpl_link: "",
        il_nomorizin: "",
        date: new Date()
      },
      dates: {
        simple: new Date("2020-10-10")
      },
      isTable: true,
      isForm: false,
      propsToSearch: ["waktu"],
      tableColumns: [
        // {
        //   align: "center",
        //   prop: "IL_DATE_FORMATTED",
        //   label: "Waktu",
        //   minWidth: 220,
        // },
        {
          align: "left",
          prop: "UK_NAME",
          label: "Unit Kerja",
          minWidth: 220
        },
        {
          align: "left",
          prop: "IL_NAME",
          label: "Izin Lingkungan",
          minWidth: 599
        }
      ],
      tableData: [],
      selectedRows: [],
      selects: {
        unitId: null
      },
      cabang
    };
  },
  methods: {
    deleteData(id) {
      swal
        .fire({
          title: "Apakah anda yakin untuk menghapus?",
          showCancelButton: true,
          confirmButtonText: `Hapus`,
          cancelButtonText: `Kembali`,
          confirmButtonColor: "#d33",
          cancelButtonColor: "#3085d6",
          icon: "question"
        })
        .then(result => {
          /* Read more about isConfirmed, isDenied below */
          if (result.isConfirmed) {
            axios
              .delete("http://45.127.133.75:3000/api/iems/il/" + id)
              .then(res => {
                swal
                  .fire({
                    title: `Success`,
                    text: "Berhasil terhapus!",
                    buttonsStyling: false,
                    confirmButtonClass: "btn btn-success",
                    icon: "success"
                  })
                  .then(result => {
                    this.getList(this.$store.getters.getUKId);
                    if (!this.$store.getters.isAdmin) {
                      this.formIzinLingkungan.uk_id = this.$store.getters.getUKId;
                    }
                    this.$router.push("daftar");
                  });
              })
              .catch(err => {
                console.log(err);
                this.swalError("Terjadi Kesalahan " + err);
              });
          }
        });
    },
    handleFileUpload() {
      this.file = this.$refs.file.files[0];
    },
    handlerDate(date) {
      return moment(date * 1000).format("DD/MM/YYYY");
    },
    cabangShift(data) {
      let tempCabang = [];
      for (let i = 1; i < data.length; i++) {
        tempCabang.push(data[i]);
      }
      return tempCabang;
    },
    toUpdateForm() {
      this.isUpdate = true;
      this.formIzinLingkungan = this.formIzinLingkunganDetail;
      this.formIzinLingkungan.date = this.formIzinLingkungan.date.toString();
      this.$router.push("form");
    },
    setFalsePeta() {
      this.allPeta.petaZonasi = false;
      this.allPeta.petaBatas = false;
      this.allPeta.petaRKL = false;
      this.allPeta.petaRPL = false;
    },
    viewPetaZonasi() {
      this.setFalsePeta();
      this.allPeta.petaZonasi = true;
    },
    viewPetaBatas() {
      this.setFalsePeta();
      this.allPeta.petaBatas = true;
    },
    viewPetaRKL() {
      this.setFalsePeta();
      this.allPeta.petaRKL = true;
    },
    viewPetaRPL() {
      this.setFalsePeta();
      this.allPeta.petaRPL = true;
    },
    toDetail(row, col, event) {
      this.tempEdit = row;
      console.log(row);
      this.formIzinLingkunganDetail.uk_id = row.UNITKERJA_ID;
      this.formIzinLingkunganDetail.il_name = row.IL_NAME;
      this.formIzinLingkunganDetail.il_nomorizin = row.IL_NOMORIZIN;
      this.formIzinLingkunganDetail.date = row.IL_DATE;
      this.formIzinLingkunganDetail.il_mapzonasi_link = row.IL_MAPZONASI_LINK;
      this.formIzinLingkunganDetail.il_mapbaswil_link = row.IL_MAPBASWIL_LINK;
      this.formIzinLingkunganDetail.il_maprkl_link = row.IL_MAPRKL_LINK;
      this.formIzinLingkunganDetail.il_maprpl_link = row.IL_MAPRPL_LINK;
      this.formIzinLingkunganDetail.il_content = row.IL_CONTENT;

      this.$router.push("detail");
    },
    swalWarning(message) {
      swal.fire({
        title: `Warning`,
        text: message,
        buttonsStyling: false,
        confirmButtonClass: "btn btn-warning",
        icon: "warning"
      });
    },
    swalError(message) {
      swal.fire({
        title: `Error`,
        text: message,
        buttonsStyling: false,
        confirmButtonClass: "btn btn-error",
        icon: "error"
      });
    },
    submitForm() {
      if (!this.formIzinLingkungan.uk_id) {
        this.swalError("Silakan isi Unit Kerja terlebih dahulu!");
        return;
      }
      const originForm = {
        uk_id: null,
        il_name: "",
        il_content: "",
        il_mapzonasi_link: "",
        il_mapbaswil_link: "",
        il_maprkl_link: "",
        il_maprpl_link: "",
        il_nomorizin: "",
        date: new Date()
      };
      let formData = this.formIzinLingkungan;
      formData.date = Number(formData.date);
      // formData.il_content = this.$refs.formHTML.content;
      axios
        .post("http://45.127.133.75:3000/api/iems/il", formData, {
          headers: { "Content-Type": "application/json" }
        })
        .then(res => {
          swal
            .fire({
              title: `Success`,
              text: "Form berhasil tersimpan!",
              buttonsStyling: false,
              confirmButtonClass: "btn btn-success",
              icon: "success"
            })
            .then(result => {
              this.formIzinLingkungan = originForm;
              this.clearImg("zonasi");
              this.clearImg("batas");
              this.clearImg("RPL");
              this.clearImg("RPL");
              this.getList(this.$store.getters.getUKId);
              if (!this.$store.getters.isAdmin) {
                this.formIzinLingkungan.uk_id = this.$store.getters.getUKId;
              }
              this.$router.push("daftar");
            });
        })
        .catch(err => {
          console.log(err);
          this.swalError(
            "Terjadi Kesalahan!! Data tidak tersimpan, silakan ulangi."
          );
        });
    },
    submitUpdateForm() {
      if (!this.formIzinLingkungan.uk_id) {
        this.swalError("Silakan isi Unit Kerja terlebih dahulu!");
        return;
      }
      const originForm = {
        uk_id: null,
        il_name: "",
        il_content: "",
        il_mapzonasi_link: "",
        il_mapbaswil_link: "",
        il_maprkl_link: "",
        il_maprpl_link: "",
        il_nomorizin: "",
        date: new Date()
      };
      let formData = this.formIzinLingkungan;
      formData.il_date = parseInt(this.formIzinLingkungan.date);
      console.log(formData);
      // formData.il_content = this.$refs.formHTML.content;
      axios
        .put(
          "http://45.127.133.75:3000/api/iems/il/" + this.tempEdit.IL_ID,
          formData,
          {
            headers: { "Content-Type": "application/json" }
          }
        )
        .then(res => {
          swal
            .fire({
              title: `Success`,
              text: "Form berhasil terupdate!",
              buttonsStyling: false,
              confirmButtonClass: "btn btn-success",
              icon: "success"
            })
            .then(result => {
              this.formIzinLingkungan = originForm;
              this.clearImg("zonasi");
              this.clearImg("batas");
              this.clearImg("RPL");
              this.clearImg("RPL");
              this.getList(this.$store.getters.getUKId);
              if (!this.$store.getters.isAdmin) {
                this.formIzinLingkungan.uk_id = this.$store.getters.getUKId;
              }
              this.isUpdate = false;
              this.$router.push("daftar");
            });
        })
        .catch(err => {
          console.log(err);
          this.swalError(
            "Terjadi Kesalahan!! Data tidak tersimpan, silakan ulangi."
          );
        });
    },

    insertName(data) {
      if (data === "zonasi") {
        this.petaZonasiName = this.$refs.fileZonasi.files[0].name;
      }
      if (data === "batas") {
        this.petaBatasName = this.$refs.fileBatas.files[0].name;
      }
      if (data === "RKL") {
        this.petaRKLName = this.$refs.fileRKL.files[0].name;
      }
      if (data === "RPL") {
        this.petaRPLName = this.$refs.fileRPL.files[0].name;
      }
    },
    clearImg(data) {
      if (data === "zonasi") {
        this.progress.petaZonasi = 0;
        this.petaZonasiName = null;
        this.formIzinLingkungan.il_mapzonasi_link = null;
      }
      if (data === "batas") {
        this.progress.petaBatas = 0;
        this.petaBatasName = null;
        this.formIzinLingkungan.il_mapbaswil_link = null;
      }
      if (data === "RKL") {
        this.progress.petaRKL = 0;
        this.petaRKLName = null;
        this.formIzinLingkungan.il_maprkl_link = null;
      }
      if (data === "RPL") {
        this.progress.petaRPL = 0;
        this.petaRPLName = null;
        this.formIzinLingkungan.il_maprpl_link = null;
      }
    },
    showForm() {
      this.isUpdate = false;
      this.getList(this.$store.getters.getUKId);
      if (!this.$store.getters.isAdmin) {
        this.formIzinLingkungan.uk_id = this.$store.getters.getUKId;
      }
      this.$router.push("form");
    },
    hideForm() {
      this.$router.push("daftar");
      if (this.$store.getters.isAdmin) {
        this.formIzinLingkungan.uk_id = null;
        this.getList(9999);
      }
    },
    getList(data) {
      let loader = this.$loading.show({ loader: "dots" });
      axios
        .get("http://45.127.133.75:3000/api/iems/il/" + data)
        .then(res => {
          let tempTable = res.data;
          tempTable.forEach(table => {
            table.IL_DATE_FORMATTED = moment(table.IL_DATE * 1000).format(
              "DD/MM/YYYY"
            );
            console.log(table.IL_DATE);
            console.log(this.handlerDate(table.IL_DATE));
          });
          console.log(tempTable);
          this.tableData = tempTable;
          loader.hide();
        })
        .catch(err => {
          console.log(err);
        });
    },
    submitFiles(data) {
      let formData = new FormData();
      let uploadNameData = data.charAt(0).toUpperCase() + data.slice(1);

      if (data === "zonasi") {
        formData.append("picture", this.$refs.fileZonasi.files[0]);
      }
      if (data === "batas") {
        formData.append("picture", this.$refs.fileBatas.files[0]);
      }
      if (data === "RKL") {
        formData.append("picture", this.$refs.fileRKL.files[0]);
      }
      if (data === "RPL") {
        formData.append("picture", this.$refs.fileRPL.files[0]);
      }

      axios
        .post("http://45.127.133.75:3000/api/iems/img", formData, {
          headers: {
            "Content-Type": "multipart/form-data"
          },
          onUploadProgress: function(progressEvent) {
            this.progress["peta" + uploadNameData] = parseInt(
              Math.round((progressEvent.loaded / progressEvent.total) * 100)
            );
          }.bind(this)
        })
        .then(res => {
          if (data === "zonasi") {
            this.formIzinLingkungan.il_mapzonasi_link =
              "http://45.127.133.75:3000" + res.data.path;
          }
          if (data === "batas") {
            this.formIzinLingkungan.il_mapbaswil_link =
              "http://45.127.133.75:3000" + res.data.path;
          }
          if (data === "RKL") {
            this.formIzinLingkungan.il_maprkl_link =
              "http://45.127.133.75:3000" + res.data.path;
          }
          if (data === "RPL") {
            this.formIzinLingkungan.il_maprpl_link =
              "http://45.127.133.75:3000" + res.data.path;
          }
        })
        .catch(err => {
          console.log(err);
          if (data === "zonasi") {
            this.petaZonasiName = "";
            this.formIzinLingkungan.il_mapzonasi_link = "";
          }
          if (data === "batas") {
            this.petaBatasName = "";
            this.formIzinLingkungan.il_mapbaswil_link = "";
          }
          if (data === "RKL") {
            this.petaRKLName = "";
            this.formIzinLingkungan.il_maprkl_link = "";
          }
          if (data === "RPL") {
            this.petaRPLName = "";
            this.formIzinLingkungan.il_maprpl_link = "";
          }
          this.swalError("Upload Gagal, Silakan Ulangi...");
        });
    }
  },
  created() {
    this.getList(this.$store.getters.getUKId);
    if (!this.$store.getters.isAdmin) {
      this.formIzinLingkungan.uk_id = this.$store.getters.getUKId;
    }
  }
};
</script>
